:root {
  --clr-link-active-color: var(--clr-color-secondary-action-500);
  --clr-link-color: var(--clr-color-action-600);
  --clr-link-hover-color: var(--clr-color-action-600);
  --clr-link-visited-color: hsl(238, 41%, 53%);
  --clr-custom-links-hover-color: var(--clr-color-neutral-200);
  --clr-link-font-weight: inherit;
  --clr-link-font-size: inherit;
  --clr-link-line-height: inherit;
  --clr-link-letter-spacing: inherit;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@-ms-viewport {
  width: device-width;
}
html {
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
  vertical-align: middle;
}

[role=button] {
  cursor: pointer;
}

a,
area,
button,
[role=button],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

button:focus {
  outline: 2px solid -webkit-focus-ring-color;
  outline-offset: 1px;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 2px solid Highlight;
  outline-offset: 1px;
}

input,
button,
select,
textarea {
  border-radius: 0;
}

input[type=radio]:disabled,
input[type=checkbox]:disabled {
  cursor: not-allowed;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  line-height: inherit;
}

input[type=search] {
  -webkit-appearance: none;
}

[hidden] {
  display: none !important;
}

dl {
  margin-bottom: 0;
  margin-top: 1rem;
}

table {
  border-spacing: 0;
}

a:link {
  /* clr-link-color */
  color: hsl(198, 100%, 32%);
  color: var(--clr-link-color, hsl(198, 100%, 32%));
}

a:visited {
  /* clr-link-visited-color */
  color: hsl(238, 41%, 53%);
  color: var(--clr-link-visited-color, hsl(238, 41%, 53%));
}
a:visited:hover {
  /* clr-link-visited-color-hover */
  color: hsl(238, 41%, 53%);
  color: var(--clr-link-visited-color-hover, hsl(238, 41%, 53%));
}

a:hover {
  /* clr-link-hover-color */
  color: hsl(198, 100%, 32%);
  color: var(--clr-link-hover-color, hsl(198, 100%, 32%));
}

