  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* clr-login-padding */
  padding: 1.2rem 3rem;
  padding: var(--clr-login-padding, 1.2rem 3rem);
  height: auto;
  min-height: 100vh;
  /* clr-login-width */
  width: 25.2rem;
  width: var(--clr-login-width, 25.2rem);
}
.login-wrapper .login .login-footer,
.login-wrapper .login .login-header {
  display: flex;
  width: 100%;
  flex-direction: row;
}
.login-wrapper .login .login-footer .actions,
.login-wrapper .login .login-header .actions {
  margin-left: auto;
}
.login-wrapper .login .login-footer {
