  /* clr-login-footer-font-size */
  font-size: inherit;
  font-size: var(--clr-login-footer-font-size, inherit);
  /* clr-login-footer-gap */
  gap: 0.9rem;
  gap: var(--clr-login-footer-gap, 0.9rem);
}
.login-wrapper .login .login-footer .copyright {
  /* clr-login-copyright-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-login-copyright-color, hsl(198, 0%, 40%));
}
.login-wrapper .login .login-body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login-wrapper .login .title {
  /* clr-login-title-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-login-title-color, hsl(198, 0%, 0%));
