  color: hsl(198, 0%, 0%);
  color: var(--clr-login-welcome-color, hsl(198, 0%, 0%));
  /* clr-login-welcome-margin */
  margin: 0;
  margin: var(--clr-login-welcome-margin, 0);
}
.login-wrapper .login .title .hint {
  /* clr-login-hint-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-login-hint-color, hsl(198, 0%, 0%));
