  margin: 1.5rem 0 0;
  margin: var(--clr-login-hint-margin, 1.5rem 0 0);
  font-size: 0.7rem;
}
.login-wrapper .login .trademark {
  /* clr-login-trademark-color */
  color: hsl(198, 0%, 0%);
