@media screen {
  section[aria-hidden=true] {
    display: none;
  }
}
[data-hidden=true] {
  display: none;
}

button.nav-link {
  border-radius: 0;
  text-transform: capitalize;
  min-width: 0;
}

.tabs-overflow {
  position: relative;
}

.tabs-overflow .nav-item {
  margin-right: 0;
}

.tab-content {
  display: inline;
}

_:-ms-fullscreen .tab-content,
:root .tab-content {
  display: inline-block;
  width: 100%;
}

.tabs-vertical {
  display: flex;
}

.tabs-vertical > .nav {
  height: auto;
  box-shadow: none;
  flex-direction: column;
  align-items: stretch;
  margin-right: 1.2rem;
  overflow: auto;
  flex-shrink: 0;
  padding: 0.2rem;
  width: 12rem;
  min-width: 2.4rem;
  /* clr-nav-vertical-gap-size */
  gap: 0;
  gap: var(--clr-nav-vertical-gap-size, 0);
}

.tabs-vertical > .nav .nav-item {
  /* clr-nav-item-margin-right */
  margin-right: 1.2rem;
  margin-right: var(--clr-nav-item-margin-right, 1.2rem);
}

.tabs-vertical > .nav .nav-link {
  text-align: left;
  /* clr-nav-vertical-link-padding */
  padding: 0 0.6rem;
  padding: var(--clr-nav-vertical-link-padding, 0 0.6rem);
  border: none;
  flex-shrink: 0;
  margin-top: 0;
  margin-left: 0;
  width: 100%;
}

.tabs-vertical > .nav .nav-link.btn {
  margin-bottom: 0.05rem;
}

.tabs-vertical > .nav .nav-link.active::before, .tabs-vertical > .nav .nav-link:hover::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  border: none;
  border-left-style: solid;
  /* clr-nav-active-border-color */
  border-left-color: hsl(198, 100%, 32%);
  border-left-color: var(--clr-nav-active-border-color, hsl(198, 100%, 32%));
  /* clr-nav-active-border-width */
  border-left-width: 0.15rem;
  border-left-width: var(--clr-nav-active-border-width, 0.15rem);
  transition: border-left 0.2s ease-in;
}

.tabs-vertical > .nav .nav-link:not(:active).active {
  /* clr-nav-selected-bg-color */
  background-color: hsl(201, 29%, 88%);
  background-color: var(--clr-nav-selected-bg-color, hsl(201, 29%, 88%));
}

.tabs-vertical > .nav .nav-link:not(.active):hover {
  /* clr-nav-hover-bg-color */
  background-color: hsl(198, 0%, 91%);
  background-color: var(--clr-nav-hover-bg-color, hsl(198, 0%, 91%));
}

