.clr-radio-wrapper {
  position: relative;
  display: flex;
}
.clr-radio-wrapper .clr-control-label {
  font-weight: normal;
  display: block;
}
.clr-radio-wrapper ~ .clr-validate-icon {
  margin-left: 0;
}
.clr-radio-wrapper ~ .clr-subtext {
  display: inline-block;
}
.clr-radio-wrapper input[type=radio] {
  position: absolute;
  opacity: 0;
  top: 0.25rem;
  left: 0;
  height: 0.8rem;
  width: 0.8rem;
}
.clr-radio-wrapper .clr-control-label {
  position: relative;
  display: block;
  margin-top: 0;
  cursor: pointer;
  /* clr-forms-radio-label-height */
  min-height: 1.2rem;
  min-height: var(--clr-forms-radio-label-height, 1.2rem);
  /* clr-forms-radio-padding-left */
  padding-left: 1.1rem;
  padding-left: var(--clr-forms-radio-padding-left, 1.1rem);
  /* clr-forms-radio-line-height */
  line-height: 1.2rem;
  line-height: var(--clr-forms-radio-line-height, 1.2rem);
  /* clr-forms-radio-label-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-forms-radio-label-color, hsl(198, 0%, 27%));
}
.clr-radio-wrapper .clr-control-label:empty {
  padding-left: 0;
}
.clr-radio-wrapper input[type=radio] + .clr-control-label::before {
  position: absolute;
  top: 0.2rem;
  left: 0;
  content: "";
  display: inline-block;
  height: 0.8rem;
  width: 0.8rem;
  border: 0.05rem solid;
  /* clr-forms-border-color */
  border-color: hsl(198, 10%, 46%);
  border-color: var(--clr-forms-border-color, hsl(198, 10%, 46%));
  border-radius: 50%;
}
.clr-radio-wrapper input[type=radio]:checked + .clr-control-label::before {
  /* clr-forms-radio-selected-shadow */
  box-shadow: inset 0 0 0 0.3rem hsl(198, 100%, 32%);
  box-shadow: var(--clr-forms-radio-selected-shadow, inset 0 0 0 0.3rem hsl(198, 100%, 32%));
  border: none;
}
.clr-radio-wrapper input[type=radio]:focus + .clr-control-label::before {
  outline: Highlight solid 0.1rem;
  outline: -webkit-focus-ring-color solid 0.1rem;
  outline-offset: 0.05rem;
}
.clr-radio-wrapper input[type=radio]:focus:checked + .clr-control-label::before {
  outline: Highlight solid 0.1rem;
  outline: -webkit-focus-ring-color solid 0.1rem;
  outline-offset: 0.05rem;
}
.clr-radio-wrapper input[type=radio]:disabled + .clr-control-label::before {
  border: 0;
  /* clr-forms-radio-disabled-shadow */
  box-shadow: inset 0 0 0 0.6rem hsl(198, 0%, 91%);
  box-shadow: var(--clr-forms-radio-disabled-shadow, inset 0 0 0 0.6rem hsl(198, 0%, 91%));
}
.clr-radio-wrapper input[type=radio]:checked:disabled + .clr-control-label::before {
  /* clr-forms-radio-disabled-background-color */
  background-color: hsl(198, 0%, 40%);
  background-color: var(--clr-forms-radio-disabled-background-color, hsl(198, 0%, 40%));
  /* clr-forms-radio-checked-disabled-shadow */
  box-shadow: inset 0 0 0 0.3rem hsl(198, 0%, 91%);
  box-shadow: var(--clr-forms-radio-checked-disabled-shadow, inset 0 0 0 0.3rem hsl(198, 0%, 91%));
}

.clr-form-control-disabled .clr-radio-wrapper .clr-control-label {
  cursor: not-allowed;
  /* clr-forms-radio-label-disabled-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-forms-radio-label-disabled-color, hsl(198, 0%, 27%));
}

.clr-error .clr-radio-wrapper input[type=radio] + .clr-control-label::before {
  /* clr-forms-invalid-color */
  border-color: hsl(9, 100%, 38%);
  border-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
}

.clr-form-compact .clr-error .clr-subtext {
  margin-left: 0;
}

.clr-form-compact .clr-radio-wrapper {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  height: 1.2rem;
}
.clr-form-compact .clr-radio-wrapper ~ .clr-subtext {
  margin-left: 0.3rem;
}
.clr-form-compact .clr-radio-wrapper .clr-control-label {
  display: inline-block;
  margin-right: 0.6rem;
  width: auto;
}
.clr-form-compact .clr-radio-wrapper .clr-control-label {
  margin-top: 0;
}

