.clr-select-wrapper,
.clr-multiselect-wrapper {
  position: relative;
  white-space: nowrap;
}
.clr-select-wrapper select,
.clr-multiselect-wrapper select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background: none;
  height: 1.2rem;
  /* clr-forms-text-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-forms-text-color, hsl(198, 0%, 0%));
  border-bottom: 0.05rem solid;
  /* clr-forms-border-color */
  border-bottom-color: hsl(198, 10%, 46%);
  border-bottom-color: var(--clr-forms-border-color, hsl(198, 10%, 46%));
  display: inline-block;
  position: relative;
  padding: 0 1.1rem 0 0.3rem;
  cursor: pointer;
  font-size: 0.65rem;
  z-index: 2;
}
.clr-select-wrapper select:focus,
.clr-multiselect-wrapper select:focus {
  outline: 0;
}
.clr-select-wrapper select:not([multiple]),
.clr-multiselect-wrapper select:not([multiple]) {
  background: linear-gradient(to bottom, transparent 95%, var(--clr-forms-focused-color, hsl(198, 100%, 32%)) 95%) no-repeat;
  background-size: 0 100%;
  transition: background-size 0.2s ease;
}
.clr-select-wrapper select:not([multiple]):focus, .clr-select-wrapper select:not([multiple]).clr-focus,
.clr-multiselect-wrapper select:not([multiple]):focus,
.clr-multiselect-wrapper select:not([multiple]).clr-focus {
  /* clr-forms-focused-color */
  border-bottom-color: hsl(198, 100%, 32%);
  border-bottom-color: var(--clr-forms-focused-color, hsl(198, 100%, 32%));
  background-size: 100% 100%;
}
.clr-select-wrapper select[multiple]:focus,
.clr-multiselect-wrapper select[multiple]:focus {
  outline: Highlight solid 0.1rem;
  outline: -webkit-focus-ring-color solid 0.1rem;
}
.clr-select-wrapper select:hover, .clr-select-wrapper select:active,
.clr-multiselect-wrapper select:hover,
.clr-multiselect-wrapper select:active {
  /* clr-forms-select-hover-border-color */
  border-color: rgba(222, 222, 222, 0.5);
  border-color: var(--clr-forms-select-hover-border-color, rgba(222, 222, 222, 0.5));
  /* clr-forms-select-hover-background */
  background: rgba(222, 222, 222, 0.5);
  background: var(--clr-forms-select-hover-background, rgba(222, 222, 222, 0.5));
}
.clr-select-wrapper select:hover:disabled, .clr-select-wrapper select:active:disabled,
.clr-multiselect-wrapper select:hover:disabled,
.clr-multiselect-wrapper select:active:disabled {
  cursor: not-allowed;
  /* clr-forms-select-disabled-border-color */
  border-color: hsl(198, 0%, 70%);
  border-color: var(--clr-forms-select-disabled-border-color, hsl(198, 0%, 70%));
}
.clr-select-wrapper select:disabled option,
.clr-multiselect-wrapper select:disabled option {
  /* clr-forms-text-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-text-disabled-color, hsl(198, 0%, 70%));
  cursor: not-allowed;
}
.clr-select-wrapper select option,
.clr-multiselect-wrapper select option {
  /* clr-forms-select-option-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-forms-select-option-color, hsl(198, 0%, 0%));
}
.clr-select-wrapper select::-ms-expand,
.clr-multiselect-wrapper select::-ms-expand {
  display: none;
}

.clr-select-wrapper {
  max-height: 1.2rem;
  display: inline-block;
}
.clr-select-wrapper::after {
  position: absolute;
  content: "";
  height: 0.5rem;
  width: 0.5rem;
  top: 0.35rem;
  right: 0.3rem;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2012%2012%22%3E%0A%20%20%20%20%3Cdefs%3E%0A%20%20%20%20%20%20%20%20%3Cstyle%3E.cls-1%7Bfill%3Ahsl(198, 0%, 70%)%3B%7D%3C%2Fstyle%3E%0A%20%20%20%20%3C%2Fdefs%3E%0A%20%20%20%20%3Ctitle%3ECaret%3C%2Ftitle%3E%0A%20%20%20%20%3Cpath%20class%3D%22cls-1%22%20d%3D%22M6%2C9L1.2%2C4.2a0.68%2C0.68%2C0%2C0%2C1%2C1-1L6%2C7.08%2C9.84%2C3.24a0.68%2C0.68%2C0%2C1%2C1%2C1%2C1Z%22%2F%3E%0A%3C%2Fsvg%3E%0A");
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
  margin: 0;
}
.clr-select-wrapper:hover::after {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2012%2012%22%3E%0A%20%20%20%20%3Cdefs%3E%0A%20%20%20%20%20%20%20%20%3Cstyle%3E.cls-1%7Bfill%3Ahsl(198, 0%, 55%)%3B%7D%3C%2Fstyle%3E%0A%20%20%20%20%3C%2Fdefs%3E%0A%20%20%20%20%3Ctitle%3ECaret%3C%2Ftitle%3E%0A%20%20%20%20%3Cpath%20class%3D%22cls-1%22%20d%3D%22M6%2C9L1.2%2C4.2a0.68%2C0.68%2C0%2C0%2C1%2C1-1L6%2C7.08%2C9.84%2C3.24a0.68%2C0.68%2C0%2C1%2C1%2C1%2C1Z%22%2F%3E%0A%3C%2Fsvg%3E%0A");
}

.clr-error .clr-select-wrapper::after,
.clr-success .clr-select-wrapper::after {
  right: 1.5rem;
}

.clr-multiselect-wrapper {
  display: flex;
}
.clr-multiselect-wrapper .clr-validate-icon {
  margin-left: 0;
}

select[multiple],
select[size] {
  padding: 0;
  /* clr-forms-select-multiple-background-color */
  background: hsl(198, 0%, 100%);
  background: var(--clr-forms-select-multiple-background-color, hsl(198, 0%, 100%));
  border: 0.05rem solid;
  /* clr-forms-select-multiple-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-forms-select-multiple-border-color, hsl(198, 0%, 80%));
  /* clr-global-borderradius */
  border-radius: 0.15rem;
  border-radius: var(--clr-global-borderradius, 0.15rem);
  height: auto;
  min-width: 6rem;
}
select[multiple]:hover, select[multiple]:active,
select[size]:hover,
select[size]:active {
  /* clr-forms-select-multiple-background-color */
  background: hsl(198, 0%, 100%);
  background: var(--clr-forms-select-multiple-background-color, hsl(198, 0%, 100%));
  /* clr-forms-select-multiple-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-forms-select-multiple-border-color, hsl(198, 0%, 80%));
}
select[multiple] option,
select[size] option {
  padding: 0.15rem 0.3rem;
  /* clr-forms-select-multiple-option-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-forms-select-multiple-option-color, hsl(198, 0%, 0%));
}

.clr-error select:not([multiple]) {
  /* clr-forms-invalid-color */
  border-bottom-color: hsl(9, 100%, 38%);
  border-bottom-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
}
.clr-error select[multiple] {
  /* clr-forms-invalid-color */
  border-color: hsl(9, 100%, 38%);
  border-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
}

.clr-success select:not([multiple]) {
  /* clr-forms-valid-color */
  border-bottom-color: hsl(93, 100%, 26%);
  border-bottom-color: var(--clr-forms-valid-color, hsl(93, 100%, 26%));
}
.clr-success select[multiple] {
  /* clr-forms-valid-color */
  border-color: hsl(93, 100%, 26%);
  border-color: var(--clr-forms-valid-color, hsl(93, 100%, 26%));
}

.clr-form-control-disabled .clr-select.disabled {
  /* clr-forms-text-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-text-disabled-color, hsl(198, 0%, 70%));
  cursor: not-allowed;
}
.clr-form-control-disabled .clr-select.disabled:hover::after {
  /* clr-forms-select-caret-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-select-caret-color, hsl(198, 0%, 70%));
}
.clr-form-control-disabled .clr-select.disabled > select,
.clr-form-control-disabled .clr-select select:disabled {
  /* clr-forms-text-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-text-disabled-color, hsl(198, 0%, 70%));
  cursor: not-allowed;
}
.clr-form-control-disabled .clr-select.disabled > select:hover,
.clr-form-control-disabled .clr-select select:disabled:hover {
  background: none;
  /* clr-forms-border-color */
  border-color: hsl(198, 10%, 46%);
  border-color: var(--clr-forms-border-color, hsl(198, 10%, 46%));
}

.clr-form-compact .clr-multiselect-wrapper {
  margin-top: 0;
}

