.clr-textarea-wrapper {
  white-space: nowrap;
  display: flex;
  /* clr-forms-textarea-wrapper-margin-top */
  margin-top: 0.3rem;
  margin-top: var(--clr-forms-textarea-wrapper-margin-top, 0.3rem);
}
.clr-textarea-wrapper .clr-validate-icon {
  margin-left: 0;
}

.clr-textarea {
  max-width: calc(100% - 0.3rem);
  height: auto;
  resize: vertical;
  border: 0.05rem solid;
  font-size: 0.65rem;
  /* clr-forms-textarea-background-color */
  background: hsl(198, 0%, 100%);
  background: var(--clr-forms-textarea-background-color, hsl(198, 0%, 100%));
  /* clr-forms-border-color */
  border-color: hsl(198, 10%, 46%);
  border-color: var(--clr-forms-border-color, hsl(198, 10%, 46%));
  /* clr-forms-text-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-forms-text-color, hsl(198, 0%, 0%));
  /* clr-forms-textarea-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-forms-textarea-border-radius, 0.15rem);
  /* clr-forms-textarea-padding */
  padding: 0.4rem 0.6rem;
  padding: var(--clr-forms-textarea-padding, 0.4rem 0.6rem);
}
.clr-textarea:focus {
  outline: Highlight solid 0.1rem;
  outline: -webkit-focus-ring-color solid 0.1rem;
  /* clr-forms-focused-color */
  outline-color: hsl(198, 100%, 32%);
  outline-color: var(--clr-forms-focused-color, hsl(198, 100%, 32%));
  /* clr-forms-focused-color */
  border-color: hsl(198, 100%, 32%);
  border-color: var(--clr-forms-focused-color, hsl(198, 100%, 32%));
}
.clr-textarea:disabled {
  /* clr-forms-text-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-text-disabled-color, hsl(198, 0%, 70%));
  cursor: not-allowed;
  /* clr-forms-textarea-disabled-background */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-forms-textarea-disabled-background, hsl(198, 0%, 100%));
  /* clr-forms-border-disabled-color */
  border-color: hsl(198, 0%, 70%);
  border-color: var(--clr-forms-border-disabled-color, hsl(198, 0%, 70%));
}

.clr-success .clr-textarea {
  /* clr-forms-valid-color */
  border-color: hsl(93, 100%, 26%);
  border-color: var(--clr-forms-valid-color, hsl(93, 100%, 26%));
  /* clr-forms-valid-color */
  outline-color: hsl(93, 100%, 26%);
  outline-color: var(--clr-forms-valid-color, hsl(93, 100%, 26%));
}

.clr-error .clr-textarea {
  /* clr-forms-invalid-color */
  border-color: hsl(9, 100%, 38%);
  border-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
  /* clr-forms-invalid-color */
  outline-color: hsl(9, 100%, 38%);
  outline-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
}

.clr-control-container textarea {
  width: auto;
}

.clr-form-control.row .clr-textarea-wrapper {
  max-width: calc(100% - 0.3rem);
}

.clr-form-compact .clr-textarea-wrapper {
  margin-top: 0;
}
.clr-form-compact .clr-textarea-wrapper .clr-textarea {
  max-width: 100%;
}
.clr-form-compact .clr-textarea-wrapper .clr-textarea ~ .clr-subtext {
  margin-left: 0.3rem;
}

