:root {
  --clr-toggle-bg-color-off: var(--clr-color-neutral-600);
  --clr-toggle-bg-color-on: var(--clr-color-success-500);
  --clr-toggle-handle-bg-color: var(--clr-color-neutral-50);
  --clr-toggle-handle-border-color: var(--clr-toggle-handle-bg-color);
  --clr-toggle-handle-border-color-on: var(--clr-toggle-handle-bg-color);
  --clr-toggle-disabled-default-border-color: var(--clr-color-neutral-400);
  --clr-toggle-disabled-default-handle-color: var(--clr-color-neutral-0);
  --clr-toggle-disabled-off-border-color: var(--clr-toggle-disabled-default-border-color);
  --clr-toggle-disabled-off-bg-color: var(--clr-toggle-disabled-default-handle-color);
  --clr-toggle-disabled-off-handle-border-color: var(--clr-toggle-disabled-default-border-color);
  --clr-toggle-disabled-on-border-color: var(--clr-toggle-disabled-default-border-color);
  --clr-toggle-disabled-on-bg-color: var(--clr-toggle-disabled-default-border-color);
  --clr-toggle-disabled-on-handle-border-color: var(--clr-toggle-disabled-default-handle-color);
  --clr-toggle-height: 1.2rem;
  --clr-toggle-switch-base-height: 0.9rem;
  --clr-toggle-switch-base-width: 1.65rem;
  --clr-toggle-base-line: 0.3rem;
  --clr-toggle-icon-margin: calc(
    2.1rem
  );
  --clr-toggle-icon-margin-negative: calc(
    -1 * (2.1rem)
  );
  --clr-toggle-switch-left-spacing: 1.25rem;
  --clr-toggle-switch-left-spacing-negative: calc(-1 * 1.25rem);
  --clr-toggle-dot-diameter: 0.7rem;
  --clr-toggle-icon-dot-place: calc(
    -1 *
      (
        1.65rem +
          0.45rem -
          0.1rem
      )
  );
  --clr-toggle-icon-top: calc(0.5 * 0.3rem);
  --clr-toggle-icon-dot-top-disabled: 0.15rem;
  --clr-toggle-icon-dot-top: calc(0.1rem + (0.5 * 0.3rem));
}

.clr-toggle-wrapper input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
}

.clr-toggle-wrapper {
  vertical-align: middle;
  position: relative;
  display: block;
  margin-right: 0.6rem;
  /* clr-toggle-height */
  height: 1.2rem;
  height: var(--clr-toggle-height, 1.2rem);
}
.clr-toggle-wrapper label {
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin-right: 0;
  /* clr-toggle-icon-margin */
  margin-left: 2.1rem;
  margin-left: var(--clr-toggle-icon-margin, 2.1rem);
  /* clr-toggle-height */
  height: 1.2rem;
  height: var(--clr-toggle-height, 1.2rem);
}
.clr-toggle-wrapper input[type=checkbox] {
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
  height: 0.8rem;
  width: 0.8rem;
  opacity: 0;
}
.clr-toggle-wrapper input[type=checkbox] + label::before {
  position: absolute;
  display: inline-block;
  content: "";
  border: 0.1rem solid;
  border-radius: 0.45rem;
  /* clr-toggle-switch-base-height */
  height: 0.9rem;
  height: var(--clr-toggle-switch-base-height, 0.9rem);
  /* clr-toggle-switch-base-width */
  width: 1.65rem;
  width: var(--clr-toggle-switch-base-width, 1.65rem);
  /* clr-toggle-bg-color-off */
  border-color: hsl(198, 0%, 55%);
  border-color: var(--clr-toggle-bg-color-off, hsl(198, 0%, 55%));
  /* clr-toggle-bg-color-off */
  background-color: hsl(198, 0%, 55%);
  background-color: var(--clr-toggle-bg-color-off, hsl(198, 0%, 55%));
  /* clr-toggle-icon-top */
  top: 0.15rem;
  top: var(--clr-toggle-icon-top, 0.15rem);
  right: 0;
  /* clr-toggle-icon-margin-negative */
  left: -2.1rem;
  left: var(--clr-toggle-icon-margin-negative, -2.1rem);
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
  transition-property: border-color, background-color;
}
.clr-toggle-wrapper input[type=checkbox]:focus + label::before {
  outline: Highlight solid 0.1rem;
  outline: -webkit-focus-ring-color solid 0.1rem;
  outline-offset: 0.05rem;
}
.clr-toggle-wrapper input[type=checkbox]:checked + label::before {
  /* clr-toggle-bg-color-on */
  border-color: hsl(93, 67%, 38%);
  border-color: var(--clr-toggle-bg-color-on, hsl(93, 67%, 38%));
  /* clr-toggle-bg-color-on */
  background-color: hsl(93, 67%, 38%);
  background-color: var(--clr-toggle-bg-color-on, hsl(93, 67%, 38%));
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
  transition-property: border-color, background-color;
}
.clr-toggle-wrapper input[type=checkbox] + label::after {
  position: absolute;
  display: inline-block;
  content: "";
  border-width: 0.1rem;
  border-style: solid;
  border-radius: 50%;
  right: 0;
  /* clr-toggle-icon-dot-top */
  top: 0.25rem;
  top: var(--clr-toggle-icon-dot-top, 0.25rem);
  /* clr-toggle-dot-diameter */
  height: 0.7rem;
  height: var(--clr-toggle-dot-diameter, 0.7rem);
  /* clr-toggle-dot-diameter */
  width: 0.7rem;
  width: var(--clr-toggle-dot-diameter, 0.7rem);
  /* clr-toggle-handle-border-color */
  border-color: hsl(198, 0%, 98%);
  border-color: var(--clr-toggle-handle-border-color, hsl(198, 0%, 98%));
  /* clr-toggle-handle-bg-color */
  background-color: hsl(198, 0%, 98%);
  background-color: var(--clr-toggle-handle-bg-color, hsl(198, 0%, 98%));
  /* clr-toggle-icon-dot-place */
  left: -2rem;
  left: var(--clr-toggle-icon-dot-place, -2rem);
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
  transition-property: right, left;
}
.clr-toggle-wrapper input[type=checkbox]:checked + label::after {
  /* clr-toggle-handle-border-color-on */
  border-color: hsl(198, 0%, 98%);
  border-color: var(--clr-toggle-handle-border-color-on, hsl(198, 0%, 98%));
  /* clr-toggle-icon-dot-place */
  right: -2rem;
  right: var(--clr-toggle-icon-dot-place, -2rem);
  /* clr-toggle-switch-left-spacing-negative */
  left: -1.25rem;
  left: var(--clr-toggle-switch-left-spacing-negative, -1.25rem);
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
  transition-property: right, left;
}
.clr-toggle-wrapper.disabled label {
  opacity: 0.4;
  cursor: not-allowed;
}
.clr-toggle-wrapper.disabled input[type=checkbox]:checked + label::before {
  /* clr-toggle-disabled-on-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-toggle-disabled-on-border-color, hsl(198, 0%, 80%));
  /* clr-toggle-disabled-on-border-color */
  background-color: hsl(198, 0%, 80%);
  background-color: var(--clr-toggle-disabled-on-border-color, hsl(198, 0%, 80%));
}
.clr-toggle-wrapper input[type=checkbox]:disabled + label {
  cursor: not-allowed;
}
.clr-toggle-wrapper input[type=checkbox]:disabled + label::before {
  /* clr-toggle-disabled-off-bg-color */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-toggle-disabled-off-bg-color, hsl(198, 0%, 100%));
  /* clr-toggle-disabled-off-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-toggle-disabled-off-border-color, hsl(198, 0%, 80%));
}
.clr-toggle-wrapper input[type=checkbox]:disabled + label::after {
  /* clr-toggle-disabled-off-bg-color */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-toggle-disabled-off-bg-color, hsl(198, 0%, 100%));
  border-style: solid;
  /* clr-toggle-disabled-off-handle-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-toggle-disabled-off-handle-border-color, hsl(198, 0%, 80%));
  /* clr-toggle-switch-base-height */
  height: 0.9rem;
  height: var(--clr-toggle-switch-base-height, 0.9rem);
  /* clr-toggle-switch-base-height */
  width: 0.9rem;
  width: var(--clr-toggle-switch-base-height, 0.9rem);
  /* clr-toggle-icon-dot-top-disabled */
  top: 0.15rem;
  top: var(--clr-toggle-icon-dot-top-disabled, 0.15rem);
}
.clr-toggle-wrapper input[type=checkbox]:checked:disabled + label::before {
  /* clr-toggle-disabled-on-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-toggle-disabled-on-border-color, hsl(198, 0%, 80%));
  /* clr-toggle-disabled-on-bg-color */
  background-color: hsl(198, 0%, 80%);
  background-color: var(--clr-toggle-disabled-on-bg-color, hsl(198, 0%, 80%));
}
.clr-toggle-wrapper input[type=checkbox]:checked:disabled + label::after {
  /* clr-toggle-disabled-on-handle-border-color */
  border-color: hsl(198, 0%, 100%);
  border-color: var(--clr-toggle-disabled-on-handle-border-color, hsl(198, 0%, 100%));
  /* clr-toggle-dot-diameter */
  height: 0.7rem;
  height: var(--clr-toggle-dot-diameter, 0.7rem);
  /* clr-toggle-dot-diameter */
  width: 0.7rem;
  width: var(--clr-toggle-dot-diameter, 0.7rem);
  /* clr-toggle-icon-dot-top */
  top: 0.25rem;
  top: var(--clr-toggle-icon-dot-top, 0.25rem);
  /* clr-toggle-switch-left-spacing-negative */
  left: -1.25rem;
  left: var(--clr-toggle-switch-left-spacing-negative, -1.25rem);
}
.clr-toggle-wrapper input[type=checkbox]:disabled + label::after {
  /* clr-toggle-icon-margin-negative */
  left: -2.1rem;
  left: var(--clr-toggle-icon-margin-negative, -2.1rem);
}
.clr-toggle-wrapper.right-label label {
  /* clr-toggle-icon-margin */
  margin-left: 2.1rem;
  margin-left: var(--clr-toggle-icon-margin, 2.1rem);
  margin-right: 0;
}
.clr-toggle-wrapper.right-label input[type=checkbox] + label::before {
  right: 0;
  /* clr-toggle-icon-margin-negative */
  left: -2.1rem;
  left: var(--clr-toggle-icon-margin-negative, -2.1rem);
}
.clr-toggle-wrapper.right-label input[type=checkbox] + label::after {
  right: 0;
  /* clr-toggle-icon-dot-place */
  left: -2rem;
  left: var(--clr-toggle-icon-dot-place, -2rem);
  transition-property: left;
}
.clr-toggle-wrapper.right-label input[type=checkbox]:checked + label::after {
  /* clr-toggle-switch-left-spacing-negative */
  left: -1.25rem;
  left: var(--clr-toggle-switch-left-spacing-negative, -1.25rem);
  transition-property: left;
}
.clr-toggle-wrapper .clr-control-label {
  display: block;
  font-weight: 400;
  line-height: 1.2rem;
}

.clr-control-inline .clr-toggle-wrapper .clr-control-label {
  display: inline-block;
  margin-right: 0.6rem;
}

.clr-toggle-right label {
  display: inline-block;
  margin-left: 0;
