:root {
  --clr-combobox-trigger-color: var(--clr-color-neutral-500);
  --clr-combobox-font-size: 0.65rem;
  --clr-combobox-font-weight: inherit;
  --clr-combobox-min-height: 1.2rem;
  --clr-combobox-min-width: 8.4rem;
  --clr-combobox-border-color: hsl(198, 0%, 91%);
  --clr-combobox-border-bottom-color: hsl(198, 10%, 46%);
  --clr-combobox-border-radius: 0.15rem;
  --clr-combobox-input-background-color: hsl(198, 0%, 95%);
  --clr-combobox-pill-background-color: hsl(198, 0%, 100%);
  --clr-combobox-pill-border-color: hsl(198, 0%, 40%);
  --clr-combobox-pill-border-radius: 0.15rem;
  --clr-combobox-pill-font-color: hsl(198, 0%, 27%);
  --clr-combobox-filter-highlight: hsl(198, 0%, 27%);
  --clr-combobox-text-color: inherit;
  --clr-combobox-trigger-font-color: hsl(198, 0%, 27%);
  --clr-combobox-hover-background-color: hsl(198, 0%, 95%);
  --clr-combobox-pill-padding: 0 0.1rem 0 0.2rem;
  --clr-combobox-pill-margin: 0.15rem 0.3rem 0 0;
  --clr-combobox-pill-remove-icon-color: hsl(198, 0%, 27%);
  --clr-combobox-active-background-color: hsl(198, 0%, 91%);
  --clr-combobox-caret-icon-size: 0.6rem;
}

.clr-combobox-form-control {
  align-items: stretch;
}
.clr-combobox-form-control .clr-error .clr-validate-icon {
  margin-left: 0;
}

.clr-focus-indicator {
  /* clr-forms-focused-color */
  background-color: hsl(198, 100%, 32%);
  background-color: var(--clr-forms-focused-color, hsl(198, 100%, 32%));
  height: 0.1rem;
  width: 0;
  transition: width 0.2s ease;
  position: absolute;
  bottom: -0.05rem;
  left: 0;
  /* clr-combobox-border-radius */
  border-bottom-left-radius: 0.15rem;
  border-bottom-left-radius: var(--clr-combobox-border-radius, 0.15rem);
  /* clr-combobox-border-radius */
  border-bottom-right-radius: 0.15rem;
  border-bottom-right-radius: var(--clr-combobox-border-radius, 0.15rem);
}

.clr-focus {
  width: 100%;
}

.clr-error .clr-focus-indicator {
  /* clr-forms-invalid-color */
  background-color: hsl(9, 100%, 38%);
  background-color: var(--clr-forms-invalid-color, hsl(9, 100%, 38%));
}

.clr-combobox-wrapper {
  position: relative;
  padding: 0 calc(var(--clr-combobox-trigger-gap, 0.6rem) + 0.6rem + var(--clr-combobox-trigger-padding-right, 0.3rem)) 0 0.3rem;
  /* clr-combobox-padding-left */
  padding-left: 0.3rem;
  padding-left: var(--clr-combobox-padding-left, 0.3rem);
  /* clr-combobox-min-height */
  min-height: 1.2rem;
  min-height: var(--clr-combobox-min-height, 1.2rem);
  /* clr-combobox-min-width */
  min-width: 8.4rem;
  min-width: var(--clr-combobox-min-width, 8.4rem);
  /* clr-combobox-font-size */
  font-size: 0.65rem;
  font-size: var(--clr-combobox-font-size, 0.65rem);
  /* clr-combobox-font-weight */
  font-weight: inherit;
  font-weight: var(--clr-combobox-font-weight, inherit);
  /* clr-forms-text-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-forms-text-color, hsl(198, 0%, 0%));
  border-bottom: 0.05rem solid;
  /* clr-forms-border-color */
  border-bottom-color: hsl(198, 10%, 46%);
  border-bottom-color: var(--clr-forms-border-color, hsl(198, 10%, 46%));
  display: inline-block;
  /* clr-combobox-input-background-color */
  background-color: hsl(198, 0%, 95%);
  background-color: var(--clr-combobox-input-background-color, hsl(198, 0%, 95%));
  border-width: 0.05rem;
  border-style: solid;
  /* clr-combobox-border-color */
  border-color: hsl(198, 0%, 91%);
  border-color: var(--clr-combobox-border-color, hsl(198, 0%, 91%));
  /* clr-combobox-border-bottom-color */
  border-bottom-color: hsl(198, 10%, 46%);
  border-bottom-color: var(--clr-combobox-border-bottom-color, hsl(198, 10%, 46%));
  /* clr-combobox-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-combobox-border-radius, 0.15rem);
}
.clr-combobox-wrapper .clr-input {
  padding-right: 0;
  /* clr-combobox-input-left */
  padding-left: 0.3rem;
  padding-left: var(--clr-combobox-input-left, 0.3rem);
  /* clr-combobox-input-height */
  max-height: 1.5rem;
  max-height: var(--clr-combobox-input-height, 1.5rem);
}
.clr-combobox-wrapper .clr-input:not(:disabled) {
  /* clr-combobox-text-color */
  color: inherit;
  color: var(--clr-combobox-text-color, inherit);
}
.clr-combobox-wrapper.multi {
  min-width: min(18rem, 100%);
  padding-bottom: 0.15rem;
}
.clr-combobox-wrapper .clr-input.clr-combobox-input:focus {
  background: none;
}
.clr-combobox-wrapper.invalid {
  border-bottom-color: hsl(9, 100%, 38%);
}
.clr-combobox-wrapper .clr-combobox-input {
  background: none;
  border-bottom: none;
  /* clr-combobox-input-align */
  vertical-align: baseline;
  vertical-align: var(--clr-combobox-input-align, baseline);
}
.clr-combobox-wrapper .clr-combobox-remove-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0.15rem 0.15rem;
  color: hsl(198, 0%, 27%);
  /* clr-combobox-pill-remove-icon-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-combobox-pill-remove-icon-color, hsl(198, 0%, 27%));
}
.clr-combobox-wrapper .label-combobox-pill {
  /* clr-combobox-pill-font-weight */
  font-weight: 400;
  font-weight: var(--clr-combobox-pill-font-weight, 400);
  /* clr-combobox-pill-font-size */
  font-size: 0.55rem;
  font-size: var(--clr-combobox-pill-font-size, 0.55rem);
  /* clr-combobox-pill-line-height */
  line-height: 0.6rem;
  line-height: var(--clr-combobox-pill-line-height, 0.6rem);
  /* clr-combobox-pill-letter-spacing */
  letter-spacing: 0.03em;
  letter-spacing: var(--clr-combobox-pill-letter-spacing, 0.03em);
  /* clr-combobox-pill-height */
  height: 1.05rem;
  height: var(--clr-combobox-pill-height, 1.05rem);
  /* clr-combobox-pill-margin */
  margin: 0.15rem 0.3rem 0 0;
  margin: var(--clr-combobox-pill-margin, 0.15rem 0.3rem 0 0);
  /* clr-combobox-pill-background-color */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-combobox-pill-background-color, hsl(198, 0%, 100%));
  border-width: 0.05rem;
  border-style: solid;
  /* clr-combobox-pill-align-items */
  align-items: baseline;
  align-items: var(--clr-combobox-pill-align-items, baseline);
  /* clr-combobox-pill-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-combobox-pill-border-radius, 0.15rem);
  /* clr-combobox-pill-border-color */
  border-color: hsl(198, 0%, 40%);
  border-color: var(--clr-combobox-pill-border-color, hsl(198, 0%, 40%));
  /* clr-combobox-pill-padding */
  padding: 0 0.1rem 0 0.2rem;
  padding: var(--clr-combobox-pill-padding, 0 0.1rem 0 0.2rem);
}
.clr-combobox-wrapper .label-combobox-pill .clr-combobox-pill-content {
  /* clr-combobox-pill-font-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-combobox-pill-font-color, hsl(198, 0%, 27%));
  font-size: 0.55rem;
  font-weight: 400;
  /* clr-combobox-pill-content-padding */
  padding: 0 0.4rem 0 0.2rem;
  padding: var(--clr-combobox-pill-content-padding, 0 0.4rem 0 0.2rem);
}
.clr-combobox-wrapper .label-combobox-pill cds-icon,
.clr-combobox-wrapper .label-combobox-pill clr-icon {
  /* clr-combobox-item-icon-override */
  margin-top: 0;
  margin-top: var(--clr-combobox-item-icon-override, 0);
}
.clr-combobox-wrapper .label-combobox-pill cds-icon[shape=window-close],
.clr-combobox-wrapper .label-combobox-pill clr-icon[shape=window-close] {
  /* clr-combobox-pill-font-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-combobox-pill-font-color, hsl(198, 0%, 27%));
}
.clr-combobox-wrapper .clr-combobox-input-wrapper {
  border: none;
  background: none;
}
.clr-combobox-wrapper:not(.disabled):hover, .clr-combobox-wrapper:focus-within {
  /* clr-combobox-hover-background-color */
  background-color: hsl(198, 0%, 95%);
  background-color: var(--clr-combobox-hover-background-color, hsl(198, 0%, 95%));
}

.clr-combobox-trigger {
  /* clr-combobox-trigger-padding-left */
  padding-left: 0.3rem;
  padding-left: var(--clr-combobox-trigger-padding-left, 0.3rem);
  /* clr-combobox-trigger-padding-right */
  padding-right: 0.3rem;
  padding-right: var(--clr-combobox-trigger-padding-right, 0.3rem);
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background: none;
  border: none;
  color: currentColor;
  cursor: pointer;
  outline: none;
}
.clr-combobox-trigger:disabled {
  color: var(--clr-btn-link-disabled-color, #666);
  opacity: 0.4;
}
.clr-combobox-trigger cds-icon[shape=angle],
.clr-combobox-trigger clr-icon[shape=angle] {
  /* clr-combobox-trigger-font-color */
  color: hsl(198, 0%, 27%);
  color: var(--clr-combobox-trigger-font-color, hsl(198, 0%, 27%));
  /* clr-combobox-caret-icon-size */
  height: 0.6rem;
  height: var(--clr-combobox-caret-icon-size, 0.6rem);
  /* clr-combobox-caret-icon-size */
  width: 0.6rem;
  width: var(--clr-combobox-caret-icon-size, 0.6rem);
  /* clr-combobox-caret-icon-size */
  min-height: 0.6rem;
  min-height: var(--clr-combobox-caret-icon-size, 0.6rem);
  /* clr-combobox-caret-icon-size */
  min-width: 0.6rem;
  min-width: var(--clr-combobox-caret-icon-size, 0.6rem);
}

.clr-combobox-options {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 6rem;
  max-width: 18rem;
  /* clr-dropdown-bg-color */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-dropdown-bg-color, hsl(198, 0%, 100%));
  /* clr-global-borderwidth */
  border-width: 0.05rem;
  border-width: var(--clr-global-borderwidth, 0.05rem);
  border-style: solid;
  /* clr-dropdown-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-dropdown-border-color, hsl(198, 0%, 80%));
  /* clr-dropdown-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-dropdown-border-radius, 0.15rem);
  box-shadow: 0 0.05rem 0.15rem rgba(140, 140, 140, 0.25);
  box-shadow: 0 0.05rem 0.15rem var(--clr-dropdown-box-shadow, rgba(140, 140, 140, 0.25));
  margin-top: 0.1rem;
  /* clr-dropdown-items-padding */
  padding: 0.6rem 0;
  padding: var(--clr-dropdown-items-padding, 0.6rem 0);
  visibility: hidden;
  z-index: 1050;
  position: static;
  max-height: 12rem;
  overflow-y: scroll;
  visibility: visible;
}
.clr-combobox-options .clr-combobox-options-loading {
  padding: 0.15rem 1.2rem;
}
.clr-combobox-options .clr-combobox-options-loading .clr-combobox-options-loading-text {
  padding-left: 0.3rem;
}
.clr-combobox-options .clr-combobox-options-empty-text {
  padding-left: 0.6rem;
}

.clr-combobox-option {
  /* clr-dropdown-item-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-dropdown-item-color, hsl(198, 0%, 40%));
  font-size: 0.7rem;
  /* clr-dropdown-item-font-weight */
  font-weight: 400;
  font-weight: var(--clr-dropdown-item-font-weight, 400);
  letter-spacing: normal;
  background: transparent;
  border: 0;
  cursor: pointer;
  display: block;
  height: auto;
  line-height: inherit;
  margin: 0;
  width: 100%;
  text-transform: none;
  /* clr-dropdown-font-size */
  font-size: inherit;
  font-size: var(--clr-dropdown-font-size, inherit);
  /* clr-dropdown-letter-spacing */
  letter-spacing: inherit;
  letter-spacing: var(--clr-dropdown-letter-spacing, inherit);
  /* clr-dropdown-line-height */
  line-height: inherit;
  line-height: var(--clr-dropdown-line-height, inherit);
}
.clr-combobox-option:hover {
  /* clr-dropdown-bg-hover-color */
  background-color: hsl(198, 0%, 91%);
  background-color: var(--clr-dropdown-bg-hover-color, hsl(198, 0%, 91%));
  /* clr-dropdown-item-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-dropdown-item-color, hsl(198, 0%, 40%));
  text-decoration: none;
}
.clr-combobox-option.active {
  /* clr-dropdown-selection-color */
  background-color: hsl(201, 29%, 88%);
  background-color: var(--clr-dropdown-selection-color, hsl(201, 29%, 88%));
  /* clr-dropdown-active-text-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-dropdown-active-text-color, hsl(198, 0%, 0%));
}
.clr-combobox-option:active {
  box-shadow: none;
}
.clr-combobox-option:focus {
  z-index: inherit;
}
.clr-combobox-option.disabled, .clr-combobox-option:disabled {
  pointer-events: none;
  opacity: 0.4;
  user-select: none;
}
.clr-combobox-option.disabled:hover, .clr-combobox-option:disabled:hover {
  background: none;
}
.clr-combobox-option.disabled:active, .clr-combobox-option:disabled:active {
  background: none;
  box-shadow: none;
}
.clr-combobox-option.clr-focus {
  /* clr-combobox-active-background-color */
  background-color: hsl(198, 0%, 91%);
  background-color: var(--clr-combobox-active-background-color, hsl(198, 0%, 91%));
}

.clr-combobox-options .clr-combobox-option {
  /* clr-dropdown-item-padding */
  padding: 0.15rem 1.2rem;
  padding: var(--clr-dropdown-item-padding, 0.15rem 1.2rem);
}
@media screen and (max-width: 576px) {
  .clr-combobox-options .clr-combobox-option {
    padding: 0.3rem 1.2rem;
  }
}

.clr-combobox-options .clr-combobox-option:hover, .clr-combobox-options .clr-combobox-option.active {
  /* clr-dropdown-item-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-dropdown-item-color, hsl(198, 0%, 40%));
}

.clr-combobox-disabled {
  /* clr-forms-text-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-forms-text-disabled-color, hsl(198, 0%, 70%));
  cursor: not-allowed;
}
.clr-combobox-disabled .clr-combobox-input,
.clr-combobox-disabled .clr-combobox-remove-btn,
.clr-combobox-disabled .clr-combobox-trigger {
  cursor: not-allowed;
}

.clr-filter-highlight b {
  font-weight: 500;
  /* clr-combobox-filter-highlight */
  color: hsl(198, 0%, 27%);
  color: var(--clr-combobox-filter-highlight, hsl(198, 0%, 27%));
}

