  --clr-stack-view-bg-color: var(--clr-color-neutral-50);
  --clr-stack-view-stack-block-border-bottom: var(--clr-color-neutral-300);
  --clr-stack-view-title-color: hsl(198, 0%, 0%);
  --clr-stack-view-color: hsl(198, 0%, 40%);
