  --clr-stack-view-stack-block-caret-color: var(--clr-global-font-color);
  --clr-stack-view-block-expanded-border-bottom-style: none;
  --clr-stack-view-border-width: 0.05rem;
  --clr-stack-view-stack-block-label-padding: 0.3rem 0.6rem;
  --clr-stack-view-key-margin: 0;
  --clr-stack-view-stack-block-caret-margin-right: 0.24rem;
  --clr-stack-view-stack-block-caret-dimension: 0.7rem;
  --clr-stack-view-block-label-before-margin: 0.35rem 0.24rem 0 0;
  --clr-stack-view-stack-children-block-label-padding-left: 1.8rem;
  --clr-stack-view-stack-children-block-content-padding-left: 0.36rem;
  --clr-stack-view-stack-title-btn-margin-right: -0.6rem;
  --clr-stack-view-stack-title-padding: 0.3rem 0;
  --clr-stack-view-stack-title-font-size: var(--clr-h4-font-size);
  --clr-stack-view-stack-title-font-weight: var(--clr-h4-font-weight);
  --clr-stack-view-stack-title-line-height: inherit;
  --clr-stack-view-stack-title-letter-spacing: inherit;
  --clr-stack-view-stack-row-font-size: 0.65rem;
  --clr-stack-view-stack-row-font-weight: 400;
  --clr-stack-view-stack-row-line-height: 1.2rem;
  --clr-stack-view-stack-row-letter-spacing: normal;
  --clr-stack-view-stack-block-expanded-caret-color: var(--clr-stack-view-stack-block-caret-color);
  --clr-stack-view-stack-block-expandable-active-caret-color: var(--clr-stack-view-stack-block-caret-color);
  --clr-stack-view-stack-block-expandable-hover-caret-color: var(--clr-stack-view-stack-block-caret-color);
}

.stack-header {
  font-weight: 400;
  display: flex;
  align-items: flex-end;
}
.stack-header .stack-title {
  display: block;
  flex: 1 1 auto;
  /* clr-stack-view-title-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-stack-view-title-color, hsl(198, 0%, 0%));
  /* clr-stack-view-stack-title-padding */
  padding: 0.3rem 0;
  padding: var(--clr-stack-view-stack-title-padding, 0.3rem 0);
  /* clr-stack-view-stack-title-font-weight */
  font-weight: 200;
  font-weight: var(--clr-stack-view-stack-title-font-weight, 200);
  /* clr-stack-view-stack-title-font-size */
  font-size: 1.08rem;
  font-size: var(--clr-stack-view-stack-title-font-size, 1.08rem);
  /* clr-stack-view-stack-title-line-height */
  line-height: inherit;
  line-height: var(--clr-stack-view-stack-title-line-height, inherit);
  /* clr-stack-view-stack-title-letter-spacing */
  letter-spacing: inherit;
  letter-spacing: var(--clr-stack-view-stack-title-letter-spacing, inherit);
}
.stack-header .stack-actions {
  display: block;
  flex: 0 0 auto;
}
.stack-header .stack-actions .stack-action {
  margin: 0 0 0.3rem 0.6rem;
}
.stack-header .stack-actions .stack-action.btn {
  min-width: 0;
  padding: 0 0.6rem;
}
.stack-header .stack-actions .stack-action.btn-link {
  /* clr-stack-view-stack-title-btn-margin-right */
  margin-right: -0.6rem;
  margin-right: var(--clr-stack-view-stack-title-btn-margin-right, -0.6rem);
}

.stack-view {
  overflow-y: auto;
  word-wrap: break-word;
  margin-top: 0;
  border-style: solid;
  /* clr-stack-view-border-width */
  border-width: 0.05rem;
  border-width: var(--clr-stack-view-border-width, 0.05rem);
  /* clr-stack-view-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-stack-view-border-color, hsl(198, 0%, 80%));
  /* clr-stack-view-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-stack-view-border-radius, 0.15rem);
  /* clr-stack-view-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-stack-view-color, hsl(198, 0%, 40%));
  /* clr-stack-view-stack-row-font-weight */
  font-weight: 400;
  font-weight: var(--clr-stack-view-stack-row-font-weight, 400);
  /* clr-stack-view-stack-row-font-size */
  font-size: 0.65rem;
  font-size: var(--clr-stack-view-stack-row-font-size, 0.65rem);
  /* clr-stack-view-stack-row-line-height */
  line-height: 1.2rem;
  line-height: var(--clr-stack-view-stack-row-line-height, 1.2rem);
  /* clr-stack-view-stack-row-letter-spacing */
  letter-spacing: normal;
  letter-spacing: var(--clr-stack-view-stack-row-letter-spacing, normal);
  /* clr-stack-view-bg-color */
  background-color: hsl(198, 0%, 98%);
  background-color: var(--clr-stack-view-bg-color, hsl(198, 0%, 98%));
  /*
    Styles specific to stack views used in a modal
  */
}
