  line-height: 1.62rem;
  line-height: var(--clr-tree-node-content-line-height, 1.62rem);
}
.clr-treenode-content:first-child {
  /* clr-tree-node-content-padding-left */
  padding-left: 1.62rem;
  padding-left: var(--clr-tree-node-content-padding-left, 1.62rem);
}
.clr-treenode-content cds-icon,
.clr-treenode-content clr-icon {
  /* clr-tree-caret-size */
  height: 0.8rem;
  height: var(--clr-tree-caret-size, 0.8rem);
  /* clr-tree-caret-size */
  width: 0.8rem;
  width: var(--clr-tree-caret-size, 0.8rem);
  /* clr-tree-caret-size */
  min-height: 0.8rem;
  min-height: var(--clr-tree-caret-size, 0.8rem);
  /* clr-tree-caret-size */
  min-width: 0.8rem;
  min-width: var(--clr-tree-caret-size, 0.8rem);
