.clr-treenode-caret {
  flex-grow: 0;
  flex-shrink: 0;
  /* clr-tree-node-touch-target */
  flex-basis: 1.62rem;
  flex-basis: var(--clr-tree-node-touch-target, 1.62rem);
  margin: 0;
  /* clr-tree-node-touch-target */
  height: 1.62rem;
  height: var(--clr-tree-node-touch-target, 1.62rem);
  /* clr-tree-node-touch-target */
  width: 1.62rem;
  width: var(--clr-tree-node-touch-target, 1.62rem);
  /* clr-tree-node-touch-target */
  min-height: 1.62rem;
  min-height: var(--clr-tree-node-touch-target, 1.62rem);
  /* clr-tree-node-touch-target */
  min-width: 1.62rem;
  min-width: var(--clr-tree-node-touch-target, 1.62rem);
  /* clr-tree-caret-padding */
  padding: 0;
  padding: var(--clr-tree-caret-padding, 0);
  background: none;
  border: none;
  /* clr-tree-node-caret-link-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-tree-node-caret-link-color, hsl(198, 0%, 70%));
  cursor: pointer;
  outline-offset: -0.25rem;
}
.clr-treenode-caret:hover {
  /* clr-tree-node-caret-link-hover-color */
  color: hsl(198, 0%, 0%);
  color: var(--clr-tree-node-caret-link-hover-color, hsl(198, 0%, 0%));
