}
.clr-treenode-caret:disabled {
  /* clr-tree-node-caret-link-disabled-color */
  color: hsl(198, 0%, 70%);
  color: var(--clr-tree-node-caret-link-disabled-color, hsl(198, 0%, 70%));
}

.clr-tree-node-caret-icon {
  /* clr-tree-caret-size */
  height: 0.8rem;
  height: var(--clr-tree-caret-size, 0.8rem);
  /* clr-tree-caret-size */
  width: 0.8rem;
  width: var(--clr-tree-caret-size, 0.8rem);
  vertical-align: middle;
}

.clr-treenode-spinner-container {
  height: 1.62rem;
  width: 1.62rem;
  padding: 0.41rem;
}

.clr-treenode-spinner {
  height: 0.8rem;
  width: 0.8rem;
  min-height: 0.8rem;
  min-width: 0.8rem;
}

.clr-treenode-children {
  /* clr-tree-node-children-margin */
  margin-left: 1.17rem;
  margin-left: var(--clr-tree-node-children-margin, 1.17rem);
  will-change: height;
  overflow-y: hidden;
}

.clr-treenode-link {
  display: inline-block;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  /* clr-tree-node-link-padding-left */
  padding-left: 0.3rem;
  padding-left: var(--clr-tree-node-link-padding-left, 0.3rem);
  background: transparent;
  border: 0;
  /* clr-tree-link-text-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-tree-link-text-color, hsl(198, 0%, 40%));
  cursor: pointer;
  line-height: inherit;
  text-align: left;
