}
.clr-tree-node-content-container > .clr-checkbox-wrapper .clr-control-label {
  /* clr-tree-node-font-size */
  font-size: inherit;
  font-size: var(--clr-tree-node-font-size, inherit);
  /* clr-tree-node-font-weight */
  font-weight: inherit;
  font-weight: var(--clr-tree-node-font-weight, inherit);
  /* clr-tree-node-letter-spacing */
  letter-spacing: inherit;
  letter-spacing: var(--clr-tree-node-letter-spacing, inherit);
}

