.datagrid {
  border-collapse: separate;
  border-style: solid;
  /* clr-table-borderwidth */
  border-width: 0.05rem;
  border-width: var(--clr-table-borderwidth, 0.05rem);
  /* clr-table-border-color */
  border-color: hsl(198, 0%, 80%);
  border-color: var(--clr-table-border-color, hsl(198, 0%, 80%));
  /* clr-table-border-radius */
  border-radius: 0.15rem;
  border-radius: var(--clr-table-border-radius, 0.15rem);
  /* clr-table-bgcolor */
  background-color: hsl(198, 0%, 100%);
  background-color: var(--clr-table-bgcolor, hsl(198, 0%, 100%));
  /* clr-table-font-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-table-font-color, hsl(198, 0%, 40%));
  /* clr-table-margin */
  margin: 1.2rem 0 0 0;
  margin: var(--clr-table-margin, 1.2rem 0 0 0);
  max-width: 100%;
  width: 100%;
}
.datagrid .datagrid-column,
.datagrid .datagrid-cell {
  border-top-style: solid;
  /* clr-table-data-cell-font-size */
  font-size: 0.65rem;
  font-size: var(--clr-table-data-cell-font-size, 0.65rem);
  /* clr-table-data-cell-line-height */
  line-height: 0.7rem;
  line-height: var(--clr-table-data-cell-line-height, 0.7rem);
  /* clr-table-data-cell-font-weight */
  font-weight: 400;
  font-weight: var(--clr-table-data-cell-font-weight, 400);
  /* clr-table-borderwidth */
  border-top-width: 0.05rem;
  border-top-width: var(--clr-table-borderwidth, 0.05rem);
  /* clr-tablerow-bordercolor */
  border-top-color: hsl(198, 0%, 91%);
  border-top-color: var(--clr-tablerow-bordercolor, hsl(198, 0%, 91%));
  /* clr-table-cell-padding */
  padding: 0.55rem 0.6rem 0.55rem;
  padding: var(--clr-table-cell-padding, 0.55rem 0.6rem 0.55rem);
  text-align: center;
  vertical-align: top;
}
.datagrid .datagrid-column.left,
.datagrid .datagrid-cell.left {
  text-align: left;
}
.datagrid .datagrid-column.left:first-child,
.datagrid .datagrid-cell.left:first-child {
  /* clr-table-cell-padding-left */
  padding-left: 0.3rem;
  padding-left: var(--clr-table-cell-padding-left, 0.3rem);
}
.datagrid .datagrid-column.right,
.datagrid .datagrid-cell.right {
  text-align: right;
}
.datagrid .datagrid-column {
  /* clr-thead-color */
  color: hsl(198, 0%, 40%);
  color: var(--clr-thead-color, hsl(198, 0%, 40%));
  letter-spacing: 0.03em;
  /* clr-table-header-cell-font-size */
  font-size: 0.55rem;
  font-size: var(--clr-table-header-cell-font-size, 0.55rem);
  /* clr-table-header-cell-line-height */
  line-height: 0.7rem;
  line-height: var(--clr-table-header-cell-line-height, 0.7rem);
  /* clr-table-header-cell-font-weight */
  font-weight: 600;
  font-weight: var(--clr-table-header-cell-font-weight, 600);
  /* clr-thead-bgcolor */
  background-color: hsl(198, 0%, 98%);
  background-color: var(--clr-thead-bgcolor, hsl(198, 0%, 98%));
  /* clr-table-vertical-align */
  vertical-align: bottom;
  vertical-align: var(--clr-table-vertical-align, bottom);
  border-bottom-style: solid;
  /* clr-table-borderwidth */
  border-bottom-width: 0.05rem;
  border-bottom-width: var(--clr-table-borderwidth, 0.05rem);
  /* clr-table-border-color */
  border-bottom-color: hsl(198, 0%, 80%);
  border-bottom-color: var(--clr-table-border-color, hsl(198, 0%, 80%));
  border-top: 0 none;
}
.datagrid .datagrid-body .datagrid-row:first-child .datagrid-cell {
  border-top: 0 none;
}
.datagrid .datagrid-body + .datagrid-body {
  border-top-style: solid;
  /* clr-table-borderwidth */
  border-top-width: 0.05rem;
  border-top-width: var(--clr-table-borderwidth, 0.05rem);
  /* clr-table-border-color */
  border-top-color: hsl(198, 0%, 80%);
  border-top-color: var(--clr-table-border-color, hsl(198, 0%, 80%));
}
.datagrid .datagrid-header .datagrid-column:first-child {
  border-radius: 0;
  /* clr-table-cornercellradius */
  border-top-left-radius: 0.1rem;
  border-top-left-radius: var(--clr-table-cornercellradius, 0.1rem);
}
.datagrid .datagrid-header .datagrid-column:last-child {
  border-radius: 0;
  /* clr-table-cornercellradius */
  border-top-right-radius: 0.1rem;
  border-top-right-radius: var(--clr-table-cornercellradius, 0.1rem);
}
.datagrid .datagrid-body:last-child .datagrid-row:last-child .datagrid-cell:first-child {
  border-radius: 0;
  /* clr-table-cornercellradius */
  border-bottom-left-radius: 0.1rem;
  border-bottom-left-radius: var(--clr-table-cornercellradius, 0.1rem);
}
.datagrid .datagrid-body:last-child .datagrid-row:last-child .datagrid-cell:last-child {
  border-radius: 0;
  /* clr-table-cornercellradius */
  border-bottom-right-radius: 0.1rem;
  border-bottom-right-radius: var(--clr-table-cornercellradius, 0.1rem);
}

.datagrid-compact .datagrid-column,
.datagrid-compact .datagrid-cell {
  /* clr-table-cell-compact-padding */
  padding: 0.3rem 0.6rem 0.25rem;
  padding: var(--clr-table-cell-compact-padding, 0.3rem 0.6rem 0.25rem);
