.main-container {
  background-color: var(--clr-global-app-background);
}

h1:not([cds-text]),
h2:not([cds-text]),
h3:not([cds-text]),
h4:not([cds-text]),
h5:not([cds-text]),
h6:not([cds-text]) {
  font-family: Inter, sans-serif !important;
}

a:link,
a:visited {
  text-decoration: none;
}

.content-area img,
.modal-content img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.modal-header .close,
.modal-header--accessible .close {
  border: none;
  background: none;
  cursor: pointer;
}

a:focus,
button:focus {
  outline-color: var(--color-primary-400);
}

.align-center {
  align-items: center;
}

.btn-link.btn-warning {
  color: var(--color-warning-500);
}
.btn-link.btn-warning:hover {
  color: var(--color-warning-600);
}

.table {
  border: none;
  margin: 0;
  background-color: transparent;
}
.table td {
  border: none;
}
.table th {
  border-bottom: 1px solid var(--color-table-header-border);
  font-size: var(--font-size-xs);
  font-weight: 600;
  text-transform: uppercase;
  position: relative;
  white-space: nowrap;
}
.table th,
.table td {
  padding: calc(var(--space-unit) * 1.5) calc(var(--space-unit) * 1);
}
.table tr td:first-of-type,
.table tr th:first-of-type {
  text-align: center;
}
@media screen and (min-width: 992px) {
  .table tr td:first-of-type,
  .table tr th:first-of-type {
    padding-inline-start: var(--surface-margin-left);
    text-align: start;
  }
}
.table th:last-of-type,
.table td:last-of-type {
  border-inline-end: 1px solid var(--color-table-header-border);
}
.table tr:first-of-type th:last-of-type {
  border-image: linear-gradient(0deg, var(--color-table-header-border), transparent) 1;
}
.table tr:last-of-type td:last-of-type {
  border-image: linear-gradient(180deg, var(--color-table-header-border), transparent) 1;
}
.table tbody td {
  border-bottom: 1px solid var(--color-table-row-separator);
}
.table tbody tr:hover {
  background-color: var(--color-table-row-hover-bg);
}

.full-label,
.compact-label {
  margin-inline-start: 6px;
}

.full-label {
  display: none;
}

.compact-label {
  display: initial;
}

button.icon-button {
  border: none;
  background: none;
  cursor: pointer;
  color: var(--color-icon-button);
  border-radius: var(--border-radius);
}
button.icon-button:hover {
  color: var(--color-icon-button-hover);
}

@media screen and (min-width: 768px) {
  .full-label {
    display: initial;
  }
  .compact-label {
    display: none;
  }
}
.cdk-overlay-container {
  z-index: 1050;
}

.card {
  margin-top: 0;
  box-shadow: none;
}
.card .card-header, .card .card-title {
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.btn.btn-link.nav-link {
  background-color: transparent;
}

clr-tabs .btn.btn-link {
  box-shadow: none;
}

.alert {
  border: 1px solid var(--color-primary-150);
  background-color: var(--color-primary-100);
  color: var(--color-primary-900);
  border-radius: var(--border-radius);
  padding: var(--space-unit);
}
.alert .alert-item {
  display: flex;
  align-items: flex-start;
  gap: 4px;
}
.alert.alert-danger {
  border-color: var(--color-chip-error-border);
  background-color: var(--color-chip-error-bg);
  color: var(--color-chip-error-text);
}
.alert.alert-warning {
  border-color: var(--color-chip-warning-border);
  background-color: var(--color-chip-warning-bg);
  color: var(--color-chip-warning-text);
}

.block {
  display: block !important;
}

.flex {
  display: flex !important;
}

.flex.center {
  align-items: center;
  justify-content: center;
}

.flex.wrap {
  flex-wrap: wrap;
}

.flex-spacer {
  flex: 1;
}

.max-w-layout {
  max-width: var(--layout-content-max-width);
}

@media screen and (max-width: 768px) {
  .sm\:hidden {
    display: none !important;
  }
}

@media screen and (max-width: 992px) {
  .md\:hidden {
    display: none !important;
  }
}

